.flip-tile-container {
  perspective: 1000px; /* Establishes a 3D perspective */
  width: 200px; /* Adjust as needed */
  height: 200px; /* Adjust as needed */
  cursor: pointer;
}

.flip-tile-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s; /* Animation duration and easing */
  transform-style: preserve-3d; /* Ensures child elements are also in 3D space */
}

.flip-tile-container.flipped .flip-tile-inner {
  transform: rotateY(180deg); /* Flips the inner container horizontally */
}

.flip-tile-front,
.flip-tile-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden; /* Hides the back of the element when facing away */
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ccc;
  background-color: #f0f0f0;
}

.flip-tile-back {
  transform: rotateY(180deg); /* Rotates the back content to be visible when flipped */
  background-color: #d0d0d0;
}

.flip-tile-container img {
  width: 200px;
  max-height: 200px;
}