.modal-overlay {
    pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  z-index: 100;
}

.modal-overlay.active {
  opacity: 1;
  pointer-events: all;
}

.modal-content {
    pointer-events: none;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  transform: translateY(-20px);
  opacity: 0;
  transition: transform 1s ease-in-out, opacity 1s ease-in-out;
  text-align: center;
}

.modal-content div {
    padding: 10px;
}

.modal-overlay.active .modal-content {
    pointer-events: all;
  transform: translateY(0);
  opacity: 1;
}

.button {

  background-color: #A49786;
  color: white;
  padding: 10px;
  border-radius: 5px;
    width: 200px;
  margin: 10px;
  margin-left: auto;
  margin-right: auto;
  cursor:pointer;
  font-size: 12pt;
  letter-spacing: 3px;
}